@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

.flx {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.flx-r {
  display: flex;
  flex-direction: row;
}

.flxx {
  flex-direction: row;
  flex-wrap: wrap;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: 0;
}

.container {
  max-width: 1300px;
  margin: 0 auto;
  padding: 0px 15px;
}

button {
  width: 100px;
  height: 30px;
  color: #1a1a1a;
  background: transparent;
  border: 1px solid black;
  border-radius: 5px;
  display: block;
  margin: 20px 1px;
  cursor: pointer;
}

button:hover {
  box-shadow: inset 10px 0 10px -5px rgba(0, 0, 0, 0.5), inset -10px 0 10px -5px rgba(0, 0, 0, 0.5);
}

nav {
  background-color: #1B2B34;
}

.nav {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.nav-menu ul {
  list-style-type: none;
  display: inline-flex;
}

.nav-menu ul li {
  width: 110px;
  height: 45px;
  border-left: 1px solid #000000;
  border-right: 1px solid #000000;
  text-align: center;
}

.nav-menu ul li a {
  text-decoration: none;
  color: white;
  font-size: 18px;
  font-weight: 500;
  margin-top: 8px;
  display: block;
}

.dropdown-menu {
  width: 330px;
}

i {
  font-size: 25px;
  margin-right: 12px;
}

.select-btn {
  display: flex;
  height: 45px;
  background: transparent;
  padding: 20px;
  font-size: 18px;
  font-weight: 400;
  align-items: center;
  cursor: pointer;
  justify-content: space-between;
  border-left: 1px solid #000000;
  border-right: 1px solid #000000;
  color: white;
}

.options {
  position: absolute;
  width: 330px;
  overflow-y: auto;
  height: auto;
  padding: 4px;
  animation-name: fadeInDown;
  animation-duration: 0.35s;
  animation-fill-mode: both;
  display: none;
}

.option {
  display: flex;
  height: 45px;
  cursor: pointer;
  padding: 0 16px;
  align-items: center;
  background: #273E4B;
  box-shadow: 0 0 10px rgba(18, 229, 18, 0.2);
  border-bottom: 1px solid rgba(59, 31, 20, 0.7);
}

.option span {
  color: white;
}

.option:hover {
  background: #343D42;
}

.select-btn i {
  font-size: 25px;
  transition: 0.3s;
}

.dropdown-menu.active .select-btn i {
  transform: rotate(-180deg);
}

.dropdown-menu.active .options {
  display: block;
  opacity: 0;
  z-index: 10;
  animation-name: fadeInUp;
  animation-duration: 0.4s;
  animation-fill-mode: both;
}

@keyframes fadeInUp {
  from {
    transform: translate3d(0, 30px, 0);
    opacity: 0;
  }

  to {
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}

@keyframes fadeInDown {
  from {
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }

  to {
    transform: translate3d(0, 20px, 0);
    opacity: 0;
  }
}

.main-content {
  max-width: 1100px;
  margin: 30px auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: center;
}

.main-content p {
  margin: 20px auto;
}

.main-content p:last-of-type {
  height: 400px;
}

.pop-up {
  background: #4B1E00;
  border-top-left-radius: 10px;
  border-bottom-right-radius: 80px 40px;
  color: white;
  width: 250px;
  height: 105px;
  position: fixed;
  top: 40%;
  left: 15%;
  transition: all 1s ease-in-out;
  transform: translateX(-320%);
  padding: 1rem;
  text-align: center;
  z-index: 999;
}

.pop-up div {
  color: white;
}

footer {
  background-color: #1B2B34;
  position: relative;
}

footer .ftr {
  height: 100px;
  color: white;
  font-family: "Arial";
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 23px;
}

footer .ftr p {
  margin-top: 30px;
  text-align: center;
}

.footer {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  height: 400px;
}

.footer div {
  margin: 30px auto;
  position: relative;
  width: 100%;
}

.footer div ul {
  list-style-type: none;
  display: block;
}

.footer div ul li {
  margin: 10px auto;
}

.footer div ul li a {
  color: white;
  font-family: "Arial";
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 23px;
  text-decoration: none;
  color: white;
}

